import React, {Component} from "react";
import {connect} from 'react-redux';
import {Expander, Section, Toggle} from 'eshop/modules/core/components/ui/Expander';
import {fetchDocuments} from "../actions/documents";
import MulticartDocumentsListComponent from './MulticartDocumentsListComponent';
import {HideWhenCartIsEmpty} from "eshop/modules/cart/components/Utils";
import {getCurrentStepId} from "../../checkout/selectors";
import {getDocumentItems} from "../selectors";
import MulticartGoodsOrdersDocuments from "eshop/modules/checkout/components/serialNumber/documents/MulticartGoodsOrdersDocuments";

class MulticartDocumentsComponent extends Component {

    componentWillMount() {
        this.props.fetchDocuments();
    }

    renderLists() {
        return this.props.lists.map(list => {
            return <MulticartDocumentsListComponent key={list.id} types={list.types} title={list.name} sectionKey={list.id}/>;
        })
    }

    render() {
        if(!this.documentsAvailable()){
            return null;
        }
        return (
            <HideWhenCartIsEmpty>
                {this.renderExpander()}
            </HideWhenCartIsEmpty>
        )
    }
    renderNotWWW(){
        return (
            <div className="l-page-row">
                <div className="l-row">
                    <div className="l-col l-col-small-12">
                        <div className="opl-console u-margin-l">
                            {this.renderExpander()}
                        </div>
                    </div>
                </div>
            </div>
        )
    }
    renderExpander(){
        const toggleHeader = <h2 className="h4 opl-section__heading">{this.props.descriptions.title}</h2>
        const sectionHeader = <div className="l-row" ><Toggle variant="arrow" header={toggleHeader} className="l-col l-col-12 " triggerClassName="opl-document__expander__trigger"><span className="opl-section__text-show">rozwiń sekcję Umowy, cenniki, regulaminy</span><span className="opl-section__text-hide">zwiń sekcję Umowy, cenniki, regulaminy</span></Toggle></div>
        return (
            <Expander scrollToSelected={false} className="l-page-row" id="document-section">
                <Section header={sectionHeader}  variant="section" className="opl-document__expander" contentClassName="opl-document__expander__content" id="document-component-section">
                    <Expander scrollToSelected={false} className="u-padding-left-l" id="document-component-subexpander">
                        {this.renderLists()}
                        {this.props.channels.sales=='POS' && <MulticartGoodsOrdersDocuments {...this.props}/>}
                    </Expander>
                </Section>
            </Expander>
        )
    }
    documentsAvailable() {
        return this.props.documentItems && this.props.documentItems.length > 0;
    }
}

const mapStateToProps = state => ({
    currentStepId: getCurrentStepId(state),
    documentItems: getDocumentItems(state)
});
const mapDispatchToProps = dispatch => ({
    fetchDocuments: () => dispatch(fetchDocuments())
});

export default connect(mapStateToProps, mapDispatchToProps)(MulticartDocumentsComponent);